import P5 from "p5";
import { getRandomIndexOfArray, randomNumber } from "./utils";
import Circle from "../shapes/circle";
import vaporWave from "../utils/colorPalettes";

const sketch = (p5: P5) => {
  p5.setup = () => {
    const canvas = p5.createCanvas(p5.windowWidth / 3, p5.windowHeight - 20);
    canvas.parent("app");
    canvas.center("horizontal");

    const circles: Circle[] = [];

    const background = "lightgrey";

    p5.background(
      p5.lerpColor(
        p5.color(vaporWave[getRandomIndexOfArray(vaporWave.length)]),
        p5.color("white"),
        0.1
      )
    );

    const colorsUsed = [];

    for (let i = 1; i < 4; i++) {
      const p = p5.width / 4;
      const circlePos = p5.createVector(p * i, p5.height / 2);
      const centerPoint = [p5.width / 2, p5.height / 2];
      let size = i % 2 == 0 ? 56 : 32;
      const color = vaporWave[getRandomIndexOfArray(vaporWave.length)];
      colorsUsed.push(color);
      circles.push(new Circle(p5, circlePos, size, color));
      if (i % 2 == 0) {
        let size = i % 2 == 0 ? 32 : 56;
        circles.push(
          new Circle(
            p5,
            p5.createVector(p5.width / 2, p5.height * 0.75),
            size,
            color
          )
        );
        circles.push(
          new Circle(
            p5,
            p5.createVector(p5.width / 2, p5.height * 0.25),
            size,
            color
          )
        );
      }
    }

    p5.draw = () => {
      circles.forEach((circle) => circle.draw());
    };
  };
};

new P5(sketch);
