import P5 from "p5";

export default class Circle {
  _p5: P5;
  _pos: P5.Vector;
  _size: number;
  _fill: number[];

  constructor(p5: P5, atPosition: P5.Vector, size: number, fill?: number[]) {
    this._p5 = p5;
    this._pos = atPosition;
    this._size = size;
    this._fill = fill || [5, 255, 161];
  }

  draw() {
    const p5 = this._p5; // just for convenience

    p5.push();

    p5.translate(this._pos);
    p5.noStroke();
    p5.fill(this._fill);
    p5.ellipse(0, 0, this._size);

    p5.pop();
  }
}
